  Overall container for the About Us page
  (applies a global font and text color)
*/
.aboutus-container {
  font-family: Arial, sans-serif;
  color: #333;
}

/* ======================
   HERO SECTION
====================== */
.aboutus-hero {
  background-image: url("https://i.postimg.cc/SsdRrXJJ/i-Stock-824369786-1.webp"); /* Example hero image */
  background-size: cover;
  background-position: center;
  position: relative;
  min-height: 300px; /* Larger hero for a more impactful look */
  display: flex;
  align-items: center;
  justify-content: center;
}

.aboutus-hero-overlay {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 2rem;
  text-align: center;
  color: #fff;
  width: 100%;
  max-width: 900px; /* optional max width for text area */
  margin: 0 auto;
  border-radius: 8px;
}

.aboutus-hero-title {
  font-size: 2rem;
  margin: 0 0 0.5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.aboutus-hero-subtitle {
  font-size: 1.1rem;
  margin: 0;
  font-weight: 300;
  opacity: 0.95;
}

/* ======================
   ABOUT US SECTION
====================== */
.aboutus-section {
  padding: 2rem 1rem;
  background-color: #f9f9f9;
}

.aboutus-heading {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b; /* brand-ish color accent */
  letter-spacing: 1px;
  text-transform: uppercase;
}

.aboutus-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.aboutus-card {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
  transition: transform 0.3s ease;
}

.aboutus-card:hover {
  transform: translateY(-3px);
}

.aboutus-card h3 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.aboutus-card p {
  margin: 0;
  font-size: 0.95rem;
  line-height: 1.5;
  color: #555;
}

/* ======================
   PROVIDER SECTION
====================== */
.aboutus-provider {
  padding: 2rem 1rem;
}

.aboutus-provider-title {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b; /* brand-ish color accent */
  letter-spacing: 1px;
  text-transform: uppercase;
}

.aboutus-provider-row {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.aboutus-provider-image {
  width: 220px;
  height: 220px;
  background-image: url("https://i.postimg.cc/J7KKyP4W/Screenshot-2025-03-08-132625.webp"); /* Example image */
  background-size: cover;
  background-position: center;
  border-radius: 8px;
  align-self: center;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
}

.aboutus-provider-info {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
  transition: transform 0.3s ease;
}

.aboutus-provider-info:hover {
  transform: translateY(-3px);
}

.aboutus-provider-info h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  text-align: center;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.aboutus-provider-info p {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 1rem;
  line-height: 1.5;
}

/* ======================
   SERVICES SECTION
====================== */
.aboutus-services {
  padding: 2rem 1rem;
  background-color: #f0f0f0;
}

.aboutus-services-title {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b; /* brand-ish color accent */
  letter-spacing: 1px;
  text-transform: uppercase;
}

.aboutus-services-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.aboutus-service-card {
  background-color: #fff;
  text-decoration: none;
  color: #333;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease;
}

.aboutus-service-card:hover {
  transform: translateY(-3px);
}

.aboutus-service-image {
  width: 100%;
  height: 150px;
  background-size: cover;
  background-position: center;
}

.aboutus-service-title {
  font-size: 1rem;
  margin: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.aboutus-service-desc {
  font-size: 0.85rem;
  color: #666;
  margin: 0 0.75rem 0.75rem;
  line-height: 1.4;
}

/* ======================
   MEDIA QUERIES
====================== */
@media (min-width: 768px) {
  /* About Us Cards in row */
  .aboutus-content {
    flex-direction: row;
  }
  .aboutus-card {
    flex: 1;
  }

  /* Provider row side by side */
  .aboutus-provider-row {
    flex-direction: row;
    align-items: center;
  }
  .aboutus-provider-image {
    flex: 0 0 250px;
    height: 250px;
  }
  .aboutus-provider-info {
    flex: 1;
    margin-left: 1rem;
  }

  /* Services grid: multiple columns */
  .aboutus-services-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}
